@import "../../assets/scss/app";

.theGame {

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 20px;

    @include desktop() {
      padding: vwD(60) 0 vwD(120);
    }

    .description {
      @include description();

      @include desktop() {
        width: vwD(910);
      }
    }

    .btn {
      margin-top: 16px;

      @include desktop() {
        margin-top: vwD(44);
      }
    }

    .cursive {
      @include cursiveDescription();
      margin-top: 10px;

      @include desktop() {
        margin-top: vwD(2);
      }

    }
  }

}