@import "../../assets/scss/app";

.app {
  position: relative;
  box-sizing: border-box;
  padding-top: 56px;


  &_menu {
    height: 100vh;
    overflow: hidden;
  }

  @include desktop(){
    padding-top: vwD(72);
  }

  .content {
    &_blur {
      filter: blur(16px)
    }
  }
}