@import "../../assets/scss/app";

.burgerMenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  z-index: 50;
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0;//144px;
  transform: translate(0, -100%);
  transition: 0.5s;

  @include desktop() {
    padding-top: 0;//vwD(213);
  }

  &_open {
    transform: translate(0, 0);
  }

  .links {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .linkWrapper {

      &:not(:first-child) {
        margin-top: 57px;

        @include desktop() {
          margin-top: vwD(80);
        }
      }

      .link {
        display: block;
        position: relative;
        text-decoration: none;


        &:hover {
          .arrowIcon {
            opacity: 1;
          }
        }

        .step {
          user-select: none;
          position: absolute;
          left: 50%;
          top: 0;
          transform: translate(-50%, -100%);
          font-weight: 300;
          font-size: 14px;
          line-height: 17px;
          text-transform: uppercase;

          @include desktop() {
            font-size: vwD(18);
            line-height: vwD(22);
          }
        }

        .label {
          user-select: none;
          @include Federo();
          font-size: 42px;
          line-height: 130%;
          text-align: center;
          text-transform: uppercase;

          @include desktop() {
            font-size: vwD(80);
          }
        }

        .arrowIcon {
          @include onlyDesktop() {
            position: absolute;
            top: 50%;
            right: vwD(-30);
            transform: translate(100%, -50%);
            opacity: 0;
            transition: 0.1s;
            width: vwD(54);
            height: vwD(54);

            svg {
              width: vwD(54);
              height: vwD(54);
            }
          }


        }

      }
    }


  }

  .socialLinks {
    @include onlyMobile(flex) {
      justify-content: center;
      position: absolute;
      left: 50%;
      bottom: 34px;
      transform: translate(-50%, 0);
    }

    .link {
      width: 24px;
      height: 24px;

      &:not(:first-child) {
        margin-left: 20px;
      }

      svg {
        width: 24px;
        height: 24px;
      }
    }
  }

}