@import "../../assets/scss/app";

.theTeam {

  .content {
    padding: 30px 20px 60px;

    @include desktop() {
      padding: vwD(60) vwD(285) vwD(155);
    }

    .description {
      @include description();
    }


    .team {
      margin-top: 40px;
      display: grid;
      grid-template-rows: repeat(4, 1fr);
      grid-row-gap: 40px;

      @include desktop() {
        margin-top: vwD(100);
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(2, 1fr);
        grid-gap: vwD(100);
      }

      img {
        display: block;
        margin: 0 auto;
        width: 280px;
        height: 290px;
        object-fit: cover;

        @include desktop() {
          width: vwD(384);
          height: vwD(384);
        }
      }

      .info {
        width: 280px;
        margin: 10px auto 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include desktop() {
          width: 100%;
          margin: vwD(16) 0 0;
        }

        p:nth-child(1) {
          font-size: 18px;
          line-height: 22px;

          @include desktop() {
            font-size: vwD(24);
            line-height: vwD(29);
          }
        }

        p:nth-child(2) {
          color: #A5A5A5;
          font-size: 14px;
          line-height: 160%;

          @include desktop() {
            font-size: vwD(15);
          }
        }

      }
    }

  }

}