@import "../../assets/scss/app";

.comics {
  overflow: hidden;

  .content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 87px 0 94px;

    @include desktop() {
      height: vwD(790);
      padding: vwD(60) 0 0;
    }

    .swiper {
      position: relative;
      z-index: 10;
      margin-top: 64px;
      width: 100%;

      @include desktop() {
        margin-top: vwD(96);
      }

      .slide {
        cursor: pointer;
        width: 99px;
        height: 153px;

        @include desktop() {
          width: vwD(243);
          height: vwD(376);
        }

        img {
          display: block;
          width: 99px;
          height: 153px;
          transform: scale(0.8);
          filter: grayscale(1);
          transition: 0.3s;

          @include desktop() {
            width: vwD(243);
            height: vwD(376);
            transform: scale(0.65);
          }
        }

        &_active {
          img {
            transform: scale(1);
            filter: grayscale(0);
          }
        }
      }
    }

    .frameIcon {
      position: absolute;
      z-index: 5;
      left: 50%;
      top: calc(50% + 5px);
      transform: translate(-50%, -50%);

      @include desktop() {
        top: 47%;
      }

      svg {
        width: 710px;

        @include desktop() {
          width: auto;
          height: vwD(800);
        }
      }
    }

    .control {
      margin-top: 77px;
      position: relative;
      z-index: 10;

      @include desktop() {
        margin-top: vwD(96);
      }
    }

  }
}