@import "../../assets/scss/app";

.comicsModal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background: rgba(0, 0, 0, 0.61);
  backdrop-filter: blur(16px);

  .closeBtn {
    @include button();
    @include centerRow();
    position: absolute;
    z-index: 10;
    top: 15px;
    right: 15px;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    border: 1px solid #FFF;

    @include desktop {
      width: vwD(50);
      height: vwD(50);
    }

    svg {
      width: 14px;
      height: 14px;

      @include desktop {
        width: vwD(24);
        height: vwD(24);
      }
    }
  }

  .swiper {
    width: 100%;
    height: 100%;

    .slide {
      width: 100vw;
      height: 100vh;
      overflow: hidden;

      @include desktop() {
        width: vwD(800);
      }

      .content {
        height: 100vh;
        padding: 10px 0;
        box-sizing: border-box;
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include desktop() {
          padding: vwD(70) 0;
        }

        img {
          display: block;
          width: 280px;
          height: 429px;
          transition: 0.3s;

          &:not(:first-child) {
            margin-top: 10px;

            @include desktop() {
              display: none;
            }
          }

          @include desktop() {
            width: vwD(369);
            height: vwD(567);
            transform: scale(0.63);
          }

          &:first-child {
            @include desktop() {
              transform: scale(0.63) rotate(-30deg);
            }
          }



        }





      }

      &_current {
        .content {
          img {
            @include desktop() {
              transform: scale(1);
            }

            &:first-child {
              @include desktop() {
                transform: scale(1) rotate(0);
              }

            }

            &:not(:first-child) {
              @include desktop() {
                margin-top: vwD(30);
                display: block;
              }
            }
          }

        }
      }


    }
  }

  .arrowBtn {
    position: absolute;
    z-index: 20;
    @include onlyDesktop();
    @include button();
    width: vwD(60);
    height: vwD(60);
    top: 50%;
    transform: translateY(-50%);

    svg {
      width: vwD(60);
      height: vwD(60);

      path {
        transition: 0.3s;
        fill: #A5A5A5;
      }
    }

    &:hover {
      svg path {
        fill: #FFF;
      }
    }

    &_Prev {
      left: vwD(217);
      transform: translateY(-50%) rotate(180deg);
    }

    &_Next {
      right: vwD(217);
    }
  }

}