.animatedCircle {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;

  svg {
    width: 100%;
    height: 100%;

    &:nth-child(1) {
      position: relative;
      z-index: 1;
    }

    &:nth-child(2) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }


}