@import "../../../assets/scss/app";

.buttonEllipse {
  @include button();
  position: relative;

  &:hover {

    .label {
      color: #000;
    }

    .btnHover {
      opacity: 1;
    }
  }

  .btnActive {
    position: relative;
    z-index: 1;
    width: 168px;
    height: 104px;

    @include desktop() {
      width: vwD(168);
      height: vwD(104);
    }

    svg {
      width: 168px;
      height: 104px;

      @include desktop() {
        width: vwD(168);
        height: vwD(104);
      }
    }
  }

  .btnHover {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 149px;
    height: 48px;
    opacity: 0;
    transition: 0.3s;

    @include desktop() {
      width: vwD(149);
      height: vwD(48);
    }

    svg {
      width: 149px;
      height: 48px;

      @include desktop() {
        width: vwD(149);
        height: vwD(48);
      }

      ellipse {
        transition: 0.1s;
      }
    }

    &_mouseDown svg ellipse {
      fill: #F1EDED;
    }
  }

  .label {
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #FFF;
    transition: 0.3s;

    @include desktop() {
      font-size: vwD(12);
      line-height: vwD(14);
    }
  }
}